.App {
  text-align: center;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.resetApp {
  position: absolute;
  right: 0px;
}

.hide {
  display: none !important;
}

.loading {
		-webkit-animation: rotation 2s infinite linear;
}

@-webkit-keyframes rotation {
		from {
				-webkit-transform: rotate(0deg);
		}
		to {
				-webkit-transform: rotate(359deg);
		}
}

.PlayersList {
  width: 500px;
  margin: 0px auto;
}

.ShopModal .stats {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.shopListings {
  display: -ms-flexbox;
  display: flex; 
  -ms-flex-wrap: wrap; 
      flex-wrap: wrap;
  -ms-flex-pack: center;
      justify-content: center;
}

.shopListings .paperCube {
  height: 150px;
  width: 250px;
  min-width: 200px;
  margin: 10px 20px;
  text-align: center;
}

.shopListings .paperCube h3{
  display: inline-block;
}

.shopListings .stats {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: center;
      justify-content: center;
  margin-bottom: 20px;
}

.shopListings .stats .chip {
  width: 50px !important;
}

.shopListings .stats .value {
  line-height:20px;
  font-size: 20px;
}

.shopListings .stats .fa, .shopListings .stats .value {
  color: #fff !important;
}

.shopListings .price, .shopListings .shares, .shopListings .orders {
  padding: 10px 0px 5px !important;
  margin: 0px 10px !important;

}

.shopListings .price {
  background-image: -webkit-gradient(linear,left top, left bottom,color-stop(0, #5cb85c),to(#419641));
  background-image: -webkit-linear-gradient(top,#5cb85c 0,#419641 100%);
  background-image: -o-linear-gradient(top,#5cb85c 0,#419641 100%);
  background-image: linear-gradient(to bottom,#5cb85c 0,#419641 100%);
}

.shopListings .shares {
  background-image: -webkit-gradient(linear,left top, left bottom,color-stop(0, #5bc0de),to(#2aabd2));
  background-image: -webkit-linear-gradient(top,#5bc0de 0,#2aabd2 100%);
  background-image: -o-linear-gradient(top,#5bc0de 0,#2aabd2 100%);
  background-image: linear-gradient(to bottom,#5bc0de 0,#2aabd2 100%);
}

.shopListings .orders {
  background-image: -webkit-gradient(linear,left top, left bottom,color-stop(0, #f0ad4e),to(#eb9316));
  background-image: -webkit-linear-gradient(top,#f0ad4e 0,#eb9316 100%);
  background-image: -o-linear-gradient(top,#f0ad4e 0,#eb9316 100%);
  background-image: linear-gradient(to bottom,#f0ad4e 0,#eb9316 100%);
}   

.shopListings .price span, .shopListings .shares span, .shopListings .orders span {
  line-height: 24px !important;
  margin: 0 auto !important;
  padding: 0px 0px !important;
}

.resource .name, .stock .name {
  line-height: 18px;
  color: rgba(0, 0, 0, 0.87);
  padding-left: 30px;
  font-size: 18px;
  width: 100px ;
  display: inline-block;
}

.resource_stocks_radio, .stock_stocks_radio {
  width: 100px !important;
  display: inline-block !important;
  margin-left: 40px;
}

.resource label.sell, .stock label.sell {
  line-height: 24px;
  color: rgba(0, 0, 0, 0.87);
  padding-left: 30px;
}

.radioBtns {
  display: -ms-inline-flexbox;
  display: inline-flex;
  margin-left: 30px;
  position: relative;
  top: 5px;
}


.ActionModal h3 {
  padding-bottom: 5px !important;
}



.actionMsg {
  background-image: -webkit-gradient(linear,left top, left bottom,color-stop(0, #f2dede),to(#e7c3c3));
  background-image: -webkit-linear-gradient(top,#f2dede 0,#e7c3c3 100%);
  background-image: -o-linear-gradient(top,#f2dede 0,#e7c3c3 100%);
  background-image: linear-gradient(to bottom,#f2dede 0,#e7c3c3 100%);
  padding: 15px;
  margin-bottom: 20px;
  border: 1px solid transparent;
  border-radius: 4px;
  color: #a94442;
  background-color: #f2dede;
}

.actionMsg span.fa{
  color: #d9534f !important;
  margin-right: 15px !important;
}

.ActionModal .payments {
  line-height: 24px;
  color: rgba(0, 0, 0, 0.87);
  padding-left: 40px;
  font-size: 24px;
  width: 200px;
  display: inline-block;
  position: absolute;
  margin-top: 40px;
}

.ActionModal .shipping_offices {
  line-height: 24px;
  color: rgba(0, 0, 0, 0.87);
  font-size: 24px;
  width: 100%;
  display: inline-block;
  margin-left: -20px;
  margin-bottom: 15px;
}

.player span {
  font-size: 20px;
  margin-right: 20px;
  display: inline-block;
}

.PlayersList span.player {
  text-align:left !important;
  padding: 5px 0px 5px 40px !important;
}

.PlayersList span.player > div > div {
  padding: 5px 16px !important;
}

.player .player_selected {
  width: 25px;
  min-height: 25px;
}
.player .player_name{
  width: 100px;
}
.player .player_bank{
  width: 75px;
}
.player .player_vp {
  width: 75px;
}

.playerList_header {
  width: 500px;
  padding: 5px 16px !important;
  text-align: left;
  padding-left: 40px !important;
}

.playerList_header span {
  margin-right: 20px;
  font-weight: bold;
  font-size: 16px;
  text-decoration: underline;
}

.playerList_header .player_selected {
  width: 25px;
  display: inline-block;
  min-height: 25px;
}
.playerList_header .player_name{
  width: 100px;
  display: inline-block;
}
.playerList_header .player_bank{
  width: 75px;
  display: inline-block;
}
.playerList_header .player_vp {
  width: 75px;
  display: inline-block;
}

.dividends_vp .player {
  margin: 5px 0px;
}

.dividends_vp .header {
  border-bottom: 1px solid #000;
  margin-bottom: 10px;
}

.dividends_vp .player .name {
  width: 200px;
}

.dividends_vp .player .dividends {
  width: 150px;
}

.dividends_vp .player .stock_split {
  width: 50px;
}

.radioBtns .mod {
  margin: 0px 15px 0px 10px;
}

.ShopModal .radioBtns {
  width: 250px;
}

.ShopModal .radioBtns.stock_majority{
  width: 100%;
  display: block;
}


